/* stylelint-disable */
.burger {
  --burger-width: 48px;
  --burger-height: 48px;
  --burger-line-height: 1.5px;
  --border-radius: 100px;
  position: relative;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  background: linear-gradient(180deg, #eaeaea 0%, #cbcbcb 100%);
  border-radius: var(--border-radius);
  color: rgba(var(--cl-c), 1);
  flex-shrink: 0;
  transition: all 0.3s;
  cursor: pointer;
  display: flex;
  position: relative;
  z-index: 91;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: calc(var(--burger-width) - 31px);
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  border-radius: var(--border-radius);
}
.burger::before {
  top: calc(var(--burger-height) - 31px);
}
.burger::after {
  bottom: calc(var(--burger-height) - 31px);
}
.burger__line {
  position: absolute;
  top: 50%;
  left: 50%;
  width: calc(var(--burger-width) - 31px);
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translate(-50%, -50%);
  transition: transform 0.3s ease-in-out;
  border-radius: var(--border-radius);
}
.burger.active::before {
  top: 60%;
  left: 45%;
  transform: rotate(45deg) translateX(-45%) translateY(-60%);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger.active::after {
  bottom: 60%;
  left: 45%;
  transform: rotate(-45deg) translateX(-45%);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger.active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.overlay {
  position: fixed;
  height: 100%;
  right: 0;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 20;
  padding: calc(var(--header-height) + 20px) 0;
  overflow-y: scroll;
  background: var(--overlay-bg);
  visibility: hidden;
  opacity: 0;
  transition: all 1s;
  transition-delay: 0.5s;
  pointer-events: none;
}
.overlay::-webkit-scrollbar {
  display: none;
}
.overlay.active {
  display: grid;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(16px);
  visibility: visible;
  opacity: 1;
  transition: all 0.6s;
  pointer-events: initial;
}

